<template>
  <div>
    <echarts-line :data="lineData" :loading="loading" height="400px"
      @options-init="optionsInit($event)"></echarts-line>
    <echarts-bar :data="barData" :loading="loading"></echarts-bar>
    <echarts-bar :data="barLineData" :loading="loading"></echarts-bar>
    <echarts-pie :data="pieData" :loading="loading"></echarts-pie>
  </div>
</template>

<script>
import { lineData, barData, pieData, barLineData } from './data';
import { EchartsLine, EchartsBar, EchartsPie } from '@jg/via-ui';

export default {
  name: 'echarts',
  components: { EchartsLine, EchartsBar, EchartsPie },
  data() {
    return {
      pieData: [],
      barData: [],
      lineData: [],
      barLineData: [],
      loading: false,
    };
  },
  methods: {
    optionsInit(options) {
      options.tooltip.formatter = data => {
        let tip = '';
        const { name } = data[0];
        data.forEach(v => {
          tip += `<br/>${v.marker}${v.seriesName}: ${v.value.toString()}`;
        });
        return name + tip;
      };
    },
  },
  mounted() {
    this.loading = true;
    setTimeout(() => {
      this.lineData = Object.freeze(lineData);
      this.barData = Object.freeze(barData);
      this.pieData = Object.freeze(pieData);
      this.barLineData = Object.freeze(barLineData);
      this.loading = false;
    }, 833);
  },
};
</script>
